::-webkit-scrollbar {
  width: 10px;
  height: 10px;
  margin: 2px;
}

::-webkit-scrollbar-track {
  border-radius: 4px;
}

::-webkit-scrollbar-thumb {
  border-radius: 4px;
  background: rgba(255, 255, 255, 0.1)
}

::-webkit-scrollbar-corner {
  background: transparent
}



